import React from 'react';
import './Policy.css';

const Policy = () => {
  return (
    <div className="policy">
      <h1>Policy</h1>
      <p>
        <h2>INFORMATIVA PER TRATTAMENTO DI DATI PERSONALI RACCOLTI PRESSO LINTERESSATO</h2>
 
 In relazione a quanto previsto dal Reg. UE 2016/679 (Regolamento Europeo per la protezione dei dati personali) con la presente comunichiamo le dovute informazioni in ordine al trattamento dei dati personali forniti dallinteressato.
 La presente informativa che  resa ai sensi dell art. 13 della Reg. UE 2016/679 (Regolamento Europeo per la protezione dei dati personali) e ai sensi dellart. 13 D.Lgs. 30.6.2003 n. 196 (Codice Privacy).
 
 <h2>1. TITOLARE DEL TRATTAMENTO</h2>
 Ai sensi degli artt. 4 e 24 del Reg. UE 2016/679 il titolare del trattamento  lAssociazione Montefeltro Verso Esperienze Raggiungibili.
 
 <h2>2. DATI OGGETTO DEL TRATTAMENTO</h2>
 Il Titolare del Trattamento tratta i dati personali identificativi (ad esempio, nome, cognome, ragione sociale, indirizzo, telefono, e-mail, riferimenti bancari e di pagamento), comunicati dallinteressato in occasione delladesione allassociazione.
 <h2>3. FINALIT E LICEITA DEL TRATTAMENTO</h2>
 I dati di natura personale forniti, saranno oggetto di trattamento nel rispetto delle condizioni di liceit ex art. 6 lett. b del Reg. UE 2016/679, ovvero per ladesione e la partecipazione allassociazione e lo svolgimento della attivit proposte a favore degli associati, ed in particolare:
 <br></br>- iscrizione nel libro soci;
 <br></br>- partecipazione alla vita associativa;
 <br></br>- informazione sulle attivit e sulle altre iniziative proposte;
 <br></br>- eventuale compilazione di form di raccolta dati per linvio di una richiesta informazioni al titolare del trattamento;
 <br></br>- adempiere agli obblighi contrattuali, di legge e finalit amministrativo-contabile. Ai fini dellapplicazione delle disposizioni in materia di protezione dei dati personali, i trattamenti effettuati per finalit amministrativo-contabili sono quelli connessi allo svolgimento delle attivit di natura organizzativa, amministrativa, finanziaria e contabile, a prescindere dalla natura dei dati trattati;
 <br></br>- adempiere agli obblighi previsti dalla legge, da un regolamento, dalla normativa comunitaria o da un ordine dellAutorit (come ad esempio in materia di antiriciclaggio);
 <br></br>- esercitare i diritti del Titolare, ad esempio il diritto di difesa in giudizio;
 <h2>4. DESTINATARI O CATEGORIE DI DESTINATARI DEI DATI</h2>
 I dati di natura personale forniti potranno essere comunicati a destinatari, nominati ex art. 28 del Reg. UE 2016/679, che tratteranno i dati in qualit di responsabili e/o in qualit di persone fisiche che agiscono sotto lautorit del Titolare e del Responsabile del trattamento, al fine di ottemperare ai contratti o finalit connesse. Precisamente, i dati potranno essere comunicati a destinatari appartenenti alle seguenti categorie:
 <br></br> soggetti che forniscono servizi per la gestione del sistema informatico e delle reti di comunicazione del Titolare del Trattamento;
 <br></br> studi o societ nellambito di rapporti di assistenza e consulenza;
 <br></br> autorit competenti per adempimenti di obblighi di legge e/o di disposizioni di organi pubblici, su richiesta;
 I soggetti appartenenti alle categorie suddette svolgono la funzione di Responsabile del trattamento dei dati, oppure operano in totale autonomia come distinti Titolari del trattamento.
 <h2>5. TRASFERIMENTO DATI VERSO UN PAESE TERZO E/O UNORGANIZZAZIONE INTERNAZIONALE</h2> I dati di natura personale forniti dallinteressato, non saranno trasferiti allestero allinterno o allesterno dellUnione Europea.
 
 <h2>6. MODALITA DI TRATTAMENTO</h2>
 Il trattamento dei dati personali dellinteressato  realizzato per mezzo delle operazioni indicate allart. 4 n. 2) GDPR del Reg. UE 2016/679 e precisamente: raccolta, registrazione, organizzazione, conservazione, consultazione, elaborazione, modificazione, selezione, estrazione, raffronto, utilizzo, interconnessione, blocco, comunicazione, cancellazione e distruzione dei dati. I dati personali sono sottoposti a trattamento sia cartaceo che elettronico e/o automatizzato.
 
 <h2>7. PERIODO DI CONSERVAZIONE DEI DATI</h2>
 Il trattamento sar svolto in forma automatizzata e/o manuale, con modalit e strumenti volti a garantire la massima sicurezza e riservatezza, ad opera di soggetti a ci appositamente incaricati. Nel rispetto di quanto previsto dallart. 5 comma 1 lett. e) del Reg. UE 2016/679 i dati personali raccolti verranno conservati in una forma che consenta lidentificazione degli interessati per un arco di tempo non superiore al conseguimento delle finalit per le quali i dati personali sono trattati.
 <h2>8. NATURA DEL CONFERIMENTO E RIFIUTO</h2>
 Il conferimento dei dati personali per le finalit di cui al punto 3 del presente documento informativo  necessario per dare seguito alladesione allassociazione. Il mancato conferimento dei dati personali pu comportare limpossibilit di ottenere tale adesione.
 
 <h2>9. DIRITTI DEGLI INTERESSATI</h2>
 Linteressato potr far valere i propri diritti come espressi dagli artt. 15, 16, 17, 18, 19, 20, 21, 22 del Regolamento UE 2016/679, rivolgendosi al Titolare del Trattamento, tramite allindirizzo di posta elettronica mover.asdaps@gmail.com
 <br></br>Linteressato ha il diritto, in qualunque momento di:
 <br></br> ottenere la conferma dell'esistenza o meno di dati personali che lo riguardano, anche se non ancora registrati, e la loro comunicazione in forma intelligibile;
 <br></br> ottenere l'indicazione: 
 <br></br>a) dell'origine dei dati personali; 
 <br></br>b) delle finalit e modalit del trattamento; 
 <br></br>c) della logica applicata in caso di trattamento effettuato con l'ausilio di strumenti elettronici; 
 <br></br>d) degli estremi identificativi del titolare, dei responsabili e del rappresentante designato ai sensi dell'art. 5, comma 2 Codice Privacy e art. 3, comma 1, GDPR; 
 <br></br>e) dei soggetti o delle categorie di soggetti ai quali i dati personali possono essere comunicati o che possono venirne a conoscenza in qualit di rappresentante designato nel territorio dello Stato, di responsabili o incaricati;
 <br></br> ottenere: 
 <br></br>a) l'aggiornamento, la rettificazione ovvero, quando vi ha interesse, l'integrazione dei dati; 
 <br></br>b) la cancellazione, la trasformazione in forma anonima o il blocco dei dati trattati in violazione di legge, compresi quelli di cui non  necessaria la conservazione in relazione agli scopi per i quali i dati sono stati raccolti o successivamente trattati; 
 <br></br>c) l'attestazione che le operazioni di cui alle lettere a) e b) sono state portate a conoscenza, anche per quanto riguarda il loro contenuto, di coloro ai quali i dati sono stati comunicati o diffusi, eccettuato il caso in cui tale adempimento si rivela impossibile o comporta un impiego di mezzi manifestamente sproporzionato rispetto al diritto tutelato;
 <br></br> opporsi, in tutto o in parte: 
 <br></br>a) per motivi legittimi al trattamento dei dati personali che lo riguardano, ancorch pertinenti allo scopo della raccolta; 
 <br></br>b) al trattamento di dati personali che lo riguardano a fini di invio di materiale pubblicitario o di vendita diretta o per il compimento di ricerche di mercato o di comunicazione commerciale, mediante luso di sistemi automatizzati di chiamata senza lintervento di un operatore mediante e-mail e/o mediante modalit di marketing tradizionali mediante telefono e/o posta cartacea.
 <br></br>Ove applicabili, linteressato ha altres i diritti di cui agli artt. 16-21 GDPR (Diritto di rettifica, diritto alloblio, diritto di limitazione di trattamento, diritto alla portabilit dei dati, diritto di opposizione), Fatto salvo ogni altro ricorso amministrativo e giurisdizionale, se linteressato ritiene che il trattamento dei dati che lo riguardano violi quanto previsto dal Reg. UE 2016/679, ai sensi dellart. 15 lettera f) del succitato Reg. UE 2016/679, ha il diritto di proporre reclamo al Garante per la protezione dei dati personali e, con riferimento allart. 6 paragrafo 1, lettera a) e art. 9, paragrafo 2, lettera a), ha il diritto di revocare in qualsiasi momento il consenso prestato. Nel caso di richiesta di portabilit del dato da parte dellinteressato, il Titolare del trattamento fornir in un formato di uso comune e leggibile i dati personali che lo riguardano, fatto salvo i commi 3 e 4 dellart. 20 del Reg. UE 2016/679.</p>
    </div>
  );
};

export default Policy;