import React from 'react';
import './Regolamento.css';

const Regolamento = () => {
  return (
    <div className="regolamento">
      <h1>Regolamento</h1>
      <p> <h2>REGOLAMENTO URBANIA CLIMBING FESTIVAL - PREMESSA E CARATTERISTICHE GENERALI:</h2>
La manifestazione di Street Boulder si svolge su percorsi brevi di arrampicata, da scalare senza corda posizionando il crash-pad in modo da evitare infortuni, tracciati su facciate di case, muri in pietra
appositamente predisposte e segnalate dell'associazione organizzatrice.
<h2>SICUREZZA ED EQUIPAGGIAMENTO:</h2>
Tutti gli iscritti dovranno avere l'equipaggiamento per la pratica del boulder in sicurezza, minimo un crash-pad ogni due persone. I responsabili delle zone boulder potranno in qualsiasi momento allontanare le persone non munite dei requisiti di sicurezza.
<br></br>I partecipanti dovranno pararsi a vicenda, onde evitare infortuni.
<br></br>In circostanze eccezionali, durante lo svolgimento della manifestazione il responsabile del settore potr
decidere di chiudere e di cancellare uno o pi blocchi per preservare l'incolumit degli atleti o l'integrit della parete. Chiunque venga trovato a scalare al di fuori dei percorsi di gara verr automaticamente squalificato e segnalato alle autorit competenti.
<h2>PRE-ISCRIZIONE AL CONTEST:</h2>
La pre-iscrizione al contest da diritto al pacco gara che comprende: zainetto, maglietta del contest, Freesbee, Buono birra, mappa del contest, braccialetto identificativo. A chi non ha effettuato la pre-iscrizione, non verr fornito il pacco gara.
<h2>ISCRIZIONE AL CONTEST:</h2>
Le iscrizioni al contest aprono il giorno dellevento, 09 aprile alle ore 09:00 al presso localit Porta Nuova, zona Barco Ducale presso il gazebo dellorganizzazione.
<br></br>Costo delliscrizione al contest  20,00 per gli ADULTI con pacco gara;
<br></br>Costo delliscrizione al contest 20,00 per gli ADULTI senza pacco gara dopo la chiusura delle pre iscrizioni;
<br></br>Costo delliscrizione al contest  10,00 per UNDER 18 senza pacco gara.
<br></br>Eventuali pacchi gara avanzati, verranno assegnati ai primi non pre-iscritti che si presentano alliscrizione stessa.
<br></br>Tutti gli atleti dovranno compilare il modulo di responsabilit con i propri dati anagrafici. Per i minorenni il modulo dovr essere compilato da un genitore o tutore legale che dovr essere presente sia al momento delliscrizione, sia durante la manifestazione.
<h2>IMPORTANTE:</h2>
Qualora si sorprendesse qualsiasi persona non munita di braccialetto identificativo/non iscritta al contest arrampicare, o munita di braccialetto ed iscritta al contest arrampicare fuori dalle zone contrassegnate, questa verr immediatamente allontana, squalificata dal contest e segnalata alle autorit competenti. 
<br></br>E' comunque a discrezione degli organizzatori allontanare o squalificare dal contest le persone che metteranno a repentaglio la propria incolumit o quella degli altri partecipanti.
<h2>IL CONTEST</h2>
Le zone boulder sono specificate sulla mappa e sono a disposizione dei partecipanti e monitorate dai responsabili dell'organizzazione.
<br></br>Tutti i blocchi sono contraddistinti da un'immagine numerata dove viene segnato il percorso del problema. Affinch il blocco sia valido, l'iscritto dovr partire con le mani sullo start o sit-start e dopo aver sollevato i piedi da terra dovr raggiungere il top, tenendo la posizione almeno tre secondi.
<br></br>Linizio del Contest  fissato per le ore 9:00 fino alle 17:00 (lorganizzazione si riserva di poter modificare gli orari).
</p>
    </div>
  );
};

export default Regolamento;